*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}



$primaryFont:'hass';
$primaryFontSize: 12px;
$primaryFontColor: #171717;
$primaryFontCase: uppercase;

$secondaryFont:'cinzel';
$secondaryFontSize: 136.7px;
$secondaryFontColor: #171717;
$secondaryFontCase: uppercase;




body{
    margin-left: 100px;
    margin-right: 100px;
    margin-top: 100px;

}


.line__1,
.line__2,
.line__3{

position: absolute;

top: 0;
height: 100%;
width: 1px;
border: 1px solid #CCCCCC;
    
}

.line__1{
left: calc(100% - 75%);
}

.line__2{

left: calc(100% - 50%);
}


.line__3{
left: calc(100% - 25%);
}