header{

    height: 100px;
    width: 100%;
 

    
}
.nav__wrapper{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
    width: 100%;
  
}
.nav__links{
    font-family: $primaryFont;
    list-style-type: none;
    font-size: $primaryFontSize;
    text-transform: $primaryFontCase;
    position: relative;
    width: 100%;
    text-align: right;
 
}

.nav__links a{
    color: $primaryFontColor;
    text-decoration: none;
}


.nav__links:nth-child(1)
{
    left: calc( 100% - 98.6%);
}

.nav__links:nth-child(2)
{
    left:calc(100% - 94.9%);
}

.nav__links:nth-child(3)
{
    left:calc(100% - 93.5%);
}

.hamburger{
    display: none;
}

.contact__envelope{
    position: relative;
   text-align: right;
   
}

.contact__envelope svg{
    position: relative!important;
}